
.nav-item {
  background-color: #e5e5e5;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  margin-left: 25px;
  cursor: pointer;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
}
.nav-item-active {
  background-color: #999999;
  -webkit-transform: scale(1.5, 1.5);
          transform: scale(1.5, 1.5);
}



